.timeline {
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: calc(50% - (2px / 2));
  width: 2px;
  background-color: #6c757d;
}

.timeline-container {
  position: relative;
  width: 50%;
}

.timeline-container::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  right: -8px;
  background-color: #fff;
  border: 2px solid #6c757d;
  top: 13px;
  border-radius: 50%;
  z-index: 1;
}

.timeline-container.right::after {
  left: -8px;
}

.timeline-container.left {
  padding-right: 15px;
}

.timeline-container.right {
  left: 50%;
  padding-left: 15px;
}

.timeline-container:not(:last-child) {
  margin-bottom: 15px;
}

