.timeline-right .timeline-container {
  width: 100%;
}

.timeline-left .timeline-container.right,
.timeline-right .timeline-container.right {
  left: 0%;
}

.timeline-left::before {
  left: 7px;
}

.timeline-left .timeline-container.left, .timeline-left .timeline-container.right {
  padding-left: 23px;
  padding-right: 0;
}

.timeline-left .timeline-container.left::after, .timeline-left .timeline-container.right::after {
  left: 0;
}

.timeline-right::before {
  right: 7px;
  left: auto;
}

.timeline-right .timeline-container.left, .timeline-right .timeline-container.right {
  padding-right: 23px;
  padding-left: 0;
}

.timeline-right .timeline-container.left::after, .timeline-right .timeline-container.right::after {
  right: 0;
  left: auto;
}

